<template>
	<center>
		<div v-if="!create_account" style="width:300px; border: 1px solid black; border-radius:10px">
			<div class="form-label-group">
				<label style="margin-left:10px;margin-right:10px" for="inputEmail">Username</label>
				<input type="username" id="inputUsername" class="form-control" placeholder="Username" required autofocus v-model="username">
			</div>

			<div class="form-label-group">

					<label style="margin-left:10px;margin-right:10px" for="inputPassword">Password</label>
					<input type="password" id="inputPassword" class="form-control" placeholder="Password" required v-model="password">

			</div>
			<div class="col">
				<button style="background-color:green;" class="btn btn-success btn-lg" v-on:click="checkLogin()"> Sign In </button>
				<button style="background-color:blue;" class="btn btn-success btn-lg" v-on:click="toggleCreateAccount()"> Create Account </button>
				<button style="background-color:grey;" class="btn btn-success btn-lg" v-on:click="backToEvents()"> Back to Events </button>
			</div>
		</div>
		<div v-if="create_account" style="width:300px; border: 1px solid black; border-radius:10px">
			<div class="form-label-group">
				<label style="margin-left:10px;margin-right:10px" for="inputEmail">Username</label>
				<input type="username" id="inputUsername" class="form-control" placeholder="Enter username" required autofocus v-model="addUserForm.username">
			</div>

			<div class="form-label-group">

					<label style="margin-left:10px;margin-right:10px" for="inputPassword">Password</label>
					<input type="password" id="inputPassword" class="form-control" placeholder="Enter password" required v-model="addUserForm.password">

			</div>
			<div class="col">
				<button style="background-color:light-blue;" class="btn btn-success btn-lg" v-on:click="createUser()"> Submit </button>
				<button style="background-color:red;" class="btn btn-success btn-lg" v-on:click="toggleCreateAccount()"> Back </button>
			</div>
		</div>
	</center>
</template>

<script>
import axios from 'axios'

export default{

	data(){
		return{
			username:'',
			password:'',
			is_admin: 0,
			token: null,
			find_match: false,
			create_account: false,
			addUserForm:{
				username: '',
				password: ''
			},
		}
	},

	methods:{

		checkLogin () {
			const parameters = {
				username: this.username,
				password: this.password
			}

			const path = 'https://sdprova.herokuapp.com/login'

			this.getAccount()

			axios.post(path, parameters)
				.then((res) => {
					this.logged = true
					this.token = res.data.token
					this.find_match = true
					this.$router.replace({ path: '/', query: { username: this.username, logged: this.logged, is_admin: this.is_admin, token: this.token } })
				})
				.catch((error) => {
					// eslint-disable-next-line
					console.error(error)
					this.username = ''
					alert('Username or Password incorrect')

				})
		},

		getAccount(){
			const path = 'https://sdprova.herokuapp.com/accounts'

			var users = []

			axios.get(path)
				.then((res)=>{
					users = res.data.accounts
					for(let i = 0; i < users.length; i++){
						if(users[i]['username'] == this.username){
							this.is_admin = users[i]['is_admin']
						}
					}
				})
				.catch((error)=>{
					console.error(error)
				})
		},

		toggleCreateAccount(){
			this.create_account = !this.create_account
		},

		backToEvents(){
			this.$router.replace({path: '/'})
		},

		initForm(){
			this.addUserForm.username = ''
			this.addUserForm.password = ''
		},

		createUser(){
			const path = 'https://sdprova.herokuapp.com/account'

			const parameters = {
				username: this.addUserForm.username,
				password: this.addUserForm.password
			}

			axios.post(path, parameters)
				.then((res)=> {
					alert("Account created successfully")
					this.username = this.addUserForm.username
					this.logged = true
					this.token = res.data.token
					this.find_match = true
					this.getAccount()
					this.$router.replace({ path: '/', query: { username: this.username, logged: this.logged, is_admin: this.is_admin, token: this.token } })
				})
				.catch((error)=>{
					if(error.response.status == 404){
						alert("Username already exists")
					}else{
						console.error(error)
					}
				})
		}
	}
}
</script>



