		</table>
		<h2 v-if="events_added.length > 0"> Total: {{ calculateTotal() }}€ </h2>
		<button class="btn btn-success btn-lg" v-if="events_added.length > 0" v-on:click="finalizePurchase()"> Finalize purchase </button>
