<script>
import axios from 'axios'

export default {

	created () {
		this.logged = this.$route.query.logged
		this.username = this.$route.query.username
		this.is_admin = this.$route.query.is_admin
		this.token = this.$route.query.token
		this.getEvents()
		this.getAvailableMoney()
		this.getTotalTickets()
	},

	data () {
		return {
			message: 'Events',
			tickets_bought: [],
			events: [],
			events_added: [],
			logged: false,
			username: '',
			available_money: 0,
			is_admin: 0,
			token: null,
			total_tickets: 0,
			show_events: true,
			show_cart: false,
			adding_event: false,
			updating_event: false,
			adding_artist: false,
			adding_artist_to_event: false,
			updating_artist: false,
			deleting_artist: false,
			addEventForm:{
				event_name: '',
				event_price: null,
				event_date: '',
				event_country: '',
				event_city: '',
				event_place: '',
				event_tickets: null,
			},
			editEventForm: {
				event_id: '',
				event_name: '',
				event_place: '',
				event_city: '',
				event_date: '',
				event_price: '',
				event_tickets: ''
			},
			addArtistForm: {
				artist_name: '',
				artist_country: '',
				artist_genre: ''
			},
			updateArtistForm: {
				artist_name: '',
				artist_new_name: '',
				artist_country: '',
				artist_genre: '',
			},
			deleteArtistForm: {
				artist_name: ''
			},
			adding_artist_event: null,
			deleting_artist_event: null,
		}
	},

	methods:{
		getEvents () {
			const path = 'https://sdprova.herokuapp.com/events'
			axios.get(path)
				.then(res => {
					this.events = res.data.events
				})
				.catch(error => {
					console.error(error)
				})
		},

		getImg(id){
			try{
				return require('../assets/' + id + '.jpg')
			}catch(e){
				return require('../assets/default.jpg')
			}
		},

		buyTickets(event){
			if(event.total_available_tickets <= 0){
				alert("There are no tickets available for this event.")
				return;
			}

			for (let i = 0; i < this.events_added.length; i += 1){
				if(this.events_added[i].name == event.name){
					this.tickets_bought[this.tickets_bought.indexOf(this.events_added[i])] += 1;
					return;
				}
			}
			this.events_added.push(event);
			this.tickets_bought.push(1);
			alert("Event added to cart successfully")
		},

		toggleCart(){
			if(!this.show_events){
				this.getEvents();
			}
			this.show_events = !this.show_events;
			this.adding_event = false
		},

		decreaseQuantity(i){
			if(this.tickets_bought[i] > 0){
				this.tickets_bought[i] -= 1;
				if(this.tickets_bought[i] == 0){
					this.events_added.splice(i, 1);
					this.tickets_bought.splice(i, 1);
				}
			}
			this.toggleCart();
			this.toggleCart();
		},

		increaseQuantity(i){
			this.tickets_bought[i] += 1;
			this.toggleCart();
			this.toggleCart();
		},

		deleteOrder(order){
			var index = this.events_added.indexOf(order);
			this.events_added.splice(index, 1);
			this.tickets_bought.splice(index, 1);
		},

		addPurchase (parameters, order) {
			const path = 'http://127.0.0.1:5000/orders/' + this.username
			axios.post(path, parameters, {
				auth: {username: this.token}
			})
			.then((res) => {
				this.deleteOrder(order)
				alert("Purchase completed successfully")
			})
			.catch((error) => {
				// eslint-disable-next-line
				if(error.response.status == 400){
					alert(error.response.data['message'])
				}
				console.error(error)
				this.getEvents()
			})
		},

		finalizePurchase ()  {
			for (let i = 0; i < this.events_added.length; i += 1) {
				const parameters = {
					id_event: this.events_added[i].id,
					tickets_bought: this.tickets_bought[i]
				}
				this.addPurchase(parameters, this.events_added[i]);
			}
			this.getAvailableMoney()
		},

		calculateTotal(){
			var total = 0;
			for(let i = 0; i < this.events_added.length; i += 1){
				total += this.events_added[i].price * this.tickets_bought[i];
			}

			return total;
		},

		getAvailableMoney(){
			const path = 'https://sdprova.herokuapp.com/accounts'

			var users = []

			axios.get(path)
				.then((res)=>{
					users = res.data.accounts
					for(let i = 0; i < users.length; i++){
						if(users[i]['username'] == this.username){
							this.available_money = users[i]['available_money']
						}
					}
				})
				.catch((error)=>{
					console.error(error)
				})

		},

		getTotalTickets() {
			const path = 'https://sdprova.herokuapp.com/orders/' + this.username

			var orders = []

			axios.get(path)
				.then((res)=>{
					orders = res.data.orders
					for(let i = 0; i < orders.length; i++){
						this.total_tickets += orders[i]['tickets_bought']
					}
				})
				.catch((error)=>{
					console.error(error)
				})
		},

		createEvent(){
			const path = 'https://sdprova.herokuapp.com/event'

			const parameters = {
				name: this.addEventForm.event_name,
				place: this.addEventForm.event_place,
				city: this.addEventForm.event_city,
				date: this.addEventForm.event_date,
				price: this.addEventForm.event_price,
				total_available_tickets: this.addEventForm.event_tickets
			}

			axios.post(path, parameters, {
				auth: {username: this.token}
				})
				.then((res)=> {
					alert("Event created successfully")
					this.initAddEventForm()
					this.adding_event = false
					this.show_events = true
					this.initAddEventForm()
				})
				.catch((error)=>{
					if(error.response.status == 401){
						alert("You need to be an admin to add an event")
					}else if(error.response.status == 404){
						alert("Event already exists")
					}else{
						console.error(error)
					}
				})
		},

		initAddEventForm() {
			this.addEventForm.event_name =  ''
			this.addEventForm.event_price = null
			this.addEventForm.event_date = ''
			this.addEventForm.event_country = ''
			this.addEventForm.event_city = ''
			this.addEventForm.event_place = ''
			this.addEventForm.event_tickets = null
		},

		updateEvent() {
			const path = 'https://sdprova.herokuapp.com/event/' + this.editEventForm.event_id

			const parameters = {
				name: this.editEventForm.event_name,
				place: this.editEventForm.event_place,
				city: this.editEventForm.event_city,
				date: this.editEventForm.event_date,
				price: this.editEventForm.event_price,
				total_available_tickets: this.editEventForm.event_tickets
			}

			axios.put(path, parameters, {
				auth: {username: this.token}
				})
				.then((res) => {
					alert("Event updated successfully")
					this.updating_event = false
					this.show_events = true
					this.initEditEventForm()
				})
				.catch((error) => {
					alert("There was an error updating the event")
					console.error(error)
				})
		},

		initEditEventForm() {
			this.editEventForm.event_id = null
			this.editEventForm.event_name = ''
			this.editEventForm.event_place = ''
			this.editEventForm.event_city = ''
			this.editEventForm.event_date = ''
			this.editEventForm.event_price = null
			this.editEventForm.event_tickets = null
		},

		addArtist(){
			const path = 'https://sdprova.herokuapp.com/artist'

			const parameters = {
				name: this.addArtistForm.artist_name,
				country: this.addArtistForm.artist_country,
				genre: this.addArtistForm.artist_genre
			}

			axios.post(path, parameters, {
				auth: {username: this.token}
				})
				.then((res) => {
					alert("Artist added successfully")
					this.initAddArtistForm()
					this.adding_artist = false
					this.show_events = true
				})
				.catch((error) => {
					console.error(error)
					if(error.response.status == 404){
						alert("Artist already exists")
					}else{
						alert("There was an error while adding the artist")
					}
				})
		},

		updateArtist(){
			const path = 'https://sdprova.herokuapp.com/artist'

			const parameters = {
				name: this.updateArtistForm.artist_name,
				new_name: this.updateArtistForm.artist_new_name,
				country: this.updateArtistForm.artist_country,
				genre: this.updateArtistForm.artist_genre
			}

			axios.put(path, parameters, {
				auth: {username: this.token}
				})
				.then((res) => {
					alert("Artist updated successfully")
					this.initUpdateArtistForm()
					this.updating_artist = false
					this.show_events = true
				})
				.catch((error) => {
					console.error(error)
					alert("Artist not found.")
				})
		},

		addArtistToEvent(){
			const path = 'https://sdprova.herokuapp.com/event/' + this.adding_artist_event.id + '/artist'

			const parameters = {
				name: this.addArtistForm.artist_name,
				country: this.addArtistForm.artist_country,
				genre: this.addArtistForm.artist_genre
			}

			axios.post(path, parameters, {
				auth: {username: this.token}
				})
				.then((res) => {
					alert("Artist added to event successfully")
					this.initAddArtistForm()
					this.adding_artist_to_event = false
					this.show_events = true
				})
				.catch((error) => {
					if(error.response.status == 403){
						alert("You don't have permission to add an artist")
					}else if(error.response.status == 401){
						alert("Your session has expired, please log in again")
					}else{
						alert("There was an error adding the artist")
					}
					console.error(error)
				})
		},

		initAddArtistForm(){
			this.addArtistForm.artist_name = ''
			this.addArtistForm.artist_country = ''
			this.addArtistForm.artist_genre = ''
		},

		initUpdateArtistForm(){
			this.updateArtistForm.artist_name = ''
			this.updateArtistForm.artist_new_name = ''
			this.updateArtistForm.artist_country = ''
			this.updateArtistForm.artist_genre = ''
		},

		deleteArtist(){
			var artists = []
			var id = null

			axios.get('https://sdprova.herokuapp.com/artists')
				.then((res) => {
					artists = res.data.artists
					for(let i = 0; i < artists.length; i++){
						if(artists[i]['name'] == this.deleteArtistForm.artist_name){
							id = artists[i]['id']
						}
					}

					const path = 'https://sdprova.herokuapp.com/artist/' + id

					axios.delete(path, {
						auth: {username: this.token}
						})
						.then((res) => {
							alert("Artist deleted successfully")
							this.initDeleteArtistForm()
							this.deleting_artist = false
							this.show_events = true
						})
						.catch((error) => {
							console.error(error)
						})
				})
				.catch((error) => {
					console.error(error)
				})
		},

		deleteArtistFromEvent(){
			var artists = []
			var id = null

			axios.get('https://sdprova.herokuapp.com/event/' + this.deleting_artist_event.id + '/artists')
				.then((res) => {
					artists = res.data.artists
					for(let i = 0; i < artists.length; i++){
						if(artists[i]['name'] == this.deleteArtistForm.artist_name){
							id = artists[i]['id']
						}
					}

					if(id == null){
						alert("Artist is not part of the event")
						return
					}

					const path = 'https://sdprova.herokuapp.com/event/' + this.deleting_artist_event.id + '/artist/' + id

					axios.delete(path, {
						auth: {username: this.token}
						})
						.then((res) => {
							alert("Artist removed from event successfully")
							this.initDeleteArtistForm()
							this.deleting_artist_from_event = false
							this.show_events = true
						})
						.catch((error) => {
							console.error(error)
						})
				})
				.catch((error) => {
					console.error(error)
				})
		},

		initDeleteArtistForm(){
			this.deleteArtistForm.artist_name = ''
		},

		logIn(){
			this.$router.replace({path: '/userlogin'})
		},

		logOut(){
			this.logged = false
			this.is_admin = 0
			this.username = ''
			this.available_money = 0
			this.tickets_bought = []
			this.token = null
			this.$router.replace({path: '/'})
		},

		deleteEvent(id){
			const path = 'http://127.0.0.1:5000/event/' + id

			axios.delete(path, {
				auth: {username: this.token}
				})
				.then((res) => {
					alert("Event deleted successfully")
				})
				.catch((error) => {
					console.error(error)
				})
		},
	},
}
</script>



